* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: sans-serif;
}

.container {
  width: 100%;
  max-width: 1170px;
  padding: 0 12px;
  margin: 0 auto;
}

.btn {
  line-height: 16px;
  font-size: 14px;
  font-weight: 400;
  padding: 10px 15px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.btn-outline {
  border-color: transparent;
}

.btn-primary {
  background-color: rgb(44, 96, 63);
  color: rgb(255, 255, 255);
}

.search {
  margin-bottom: 45px;
}

.search-box {
  width: 100%;
  border-radius: 5px;
  background-color: rgb(44, 96, 63);
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.search-box input {
  flex-grow: 1;
  padding: 14px 22px;
  border-radius: 5px;
  outline: none;
  border: none;
}

.search-btn .search-btn__icon {
  width: 15px;
  height: 15px;
}

.search-btn .search-btn__text {
  margin-left: 2px;
  font-size: 16px;
  line-height: 19px;
}

.content {
  padding-bottom: 50px;
}

.content-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 30px;
}

.content-main {
  flex-grow: 1;
}

.content-side {
  width: 260px;
  min-width: 260px;
}

.content-main__title {
  font-size: 24px;
  line-height: 28px;
  margin-top: 0;
  margin-bottom: 25px;
}

.content-main__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 30px;
}

.content-side__title {
  font-size: 18px;
  line-height: 21px;
  margin-top: 0;
  margin-bottom: 23px;
}

.content-side__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  gap: 25px;
  border-bottom: 1px solid rgb(229, 229, 229);
  padding-bottom: 36px;
  margin-bottom: 36px;
}

.content-side__footer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 4px;
}

.content-side__footer--item {
  color: rgb(196, 196, 196);
  margin: 0;
  font-size: 14px;
  line-height: 16px;
}

/*Product Card*/
.content-product {
  display: flex;
  flex-grow: 1;
  gap: 30px
}

.content-product__left {
  flex-basis: 60%;
}

.content-product__right {
  flex-basis: 40%;
}

.content-product__title, .content-product__price {
  font-size: 24px;
  line-height: 28px;
  margin-top: 0;
  margin-bottom: 25px;
}

.content-product__img {
  width: 100%;
}

.content-product__text {
  font-size: 14px;
  line-height: 20px;
}

.btn-large {
  width: 80%;
}

@media (max-width: 1200px) {
  .container {
      max-width: 930px;
  }
}

@media (max-width: 991px) {
  .container {
      max-width: 730px;
  }

  .content-box {
      flex-direction: column;
  }

  .content-main {
      width: 100%;
  }

  .content-side {
      width: 100%;
      min-width: none;
  }
  
  .content-side__footer {
      align-items: center;
  }

  /*Product Card*/
  .content-product {
      flex-direction: column;
      width: 100%;
  }

  .content-product__left {
      flex-basis: 100%;
      width: 100%;
  }
  
  .content-product__right {
      flex-basis: 100%;
      width: 100%;
  }

  .btn-large {
      width: 50%;
  }
}

@media (max-width: 786px) {
  .container {
      max-width: 100%;
  }

  .search-btn .search-btn__text {
      display: none;
  }

  .content-main, .content-side__title {
      display: flex;
      flex-direction: column;
      align-items: center;
  }

  .content-side__title {
      margin-top: 30px;
  }

  /*Product Card*/
  .content-product {
      text-align: center;
  }

  .btn-large{
      width: 55%;
  }
}

@media (max-width: 480px) {
  /*Product Card*/
  .btn-large{
      width: 95%;
  }
}
